export default {
	data() {
		return {
			theChecked:true,
			count:100,
		}
	},
	methods: {
		toggleRadios(){
			this.theChecked = !this.theChecked;
		},
		add(){
			this.count += 1;
		},
		reduce(){
			this.count -= 1;
		},
		toOpenChannel(){
			uni.navigateTo({
				url:"../open-channel/open-channel"
			})
		},
		toChannel(){
			uni.navigateTo({
				url:"../channel/channel"
			})
		}
	}
}
